.date {
  margin-top: 10px;
  margin-bottom: 10px;
}

.date__input {
  display: inline-block;
  vertical-align: middle;
  width: 152px;
  padding: 5px 10px;
  border: 1px solid #cccccc;
  border-radius: 4px;
  outline: none;
  overflow: hidden;
  font-size: 14px;
  line-height: 1;
}

.date__label {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}
